html {
  font-size: 62.5%;
  box-sizing: border-box;
}

*, *:before, *:after {
  box-sizing: inherit;
}

body {
  font-size: 1.8rem;
  font-family: "Bai Jamjuree", sans-serif;
}

.container {
  width: 95%;
  max-width: 1200px;
  margin: 0 auto;
}

img {
  max-width: 100%;
  display: block;
}

h1, h2 {
  color: #4c545d;
  font-weight: 600;
}

p {
  color: #9fabb2;
  font-weight: 400;
  font-size: 1.8rem;
}

h1, h2, h3 {
  margin: 0;
  line-height: 1.2;
}

h1, h2 {
  font-weight: 700;
}

h1 {
  font-size: 2rem;
}

p {
  font-size: 1.6rem;
  line-height: 1.8;
}

.header {
  background-image: url("../img/bg-header-mobile.png");
  background-repeat: no-repeat;
  background-position: top center;
  background-size: cover;
  text-align: center;
  padding: 10rem 0;
}
@media (min-width: 768px) {
  .header {
    background-image: url("../img/bg-header-desktop.png");
    background-repeat: no-repeat;
    background-position: top center;
    background-size: cover;
  }
}
.header__icon {
  margin: 0 auto;
}
.header__heading {
  padding-top: 5rem;
  font-weight: 600;
  font-size: 3rem;
}
@media (min-width: 768px) {
  .header__heading {
    font-size: 4rem;
  }
}
.header__heading span {
  display: block;
}
@media (min-width: 768px) {
  .header__heading span {
    display: inline-block;
  }
}
.header__text {
  padding: 0.5rem 2.5rem;
  margin: 1.5rem auto;
}
@media (min-width: 768px) {
  .header__text {
    font-size: 2rem;
    line-height: 1.5;
    max-width: 75rem;
    padding-bottom: 1rem;
  }
}
.header__button--iOS {
  width: 90%;
  padding: 1.5rem 4rem;
  margin: 2.5rem auto;
  border-radius: 5rem;
  background-color: #26baa4;
  display: block;
  text-decoration: none;
  color: #FFF;
  box-shadow: 0 1.5rem 2rem #a6ede2;
}
@media (min-width: 768px) {
  .header__button--iOS {
    display: inline-block;
    width: unset;
    margin: 2rem 1rem;
  }
}
.header__button--iOS:hover {
  cursor: pointer;
  background-color: #2acfb7;
}
.header__button--Mac {
  width: 90%;
  padding: 1.5rem 4rem;
  margin: 2.5rem auto;
  border-radius: 5rem;
  background-color: #6173ff;
  display: block;
  text-decoration: none;
  color: #FFF;
  box-shadow: 0 1.5rem 2rem white;
}
@media (min-width: 768px) {
  .header__button--Mac {
    display: inline-block;
    width: unset;
    margin: 2rem 1rem;
  }
}
.header__button--Mac:hover {
  cursor: pointer;
  background-color: #7a8aff;
}

.snippets {
  text-align: center;
  padding: 0 2.5rem;
}
@media (min-width: 768px) {
  .snippets {
    padding: 0 5rem;
  }
  .snippets__heading {
    font-size: 3rem;
  }
}
@media (min-width: 768px) and (min-width: 768px) {
  .snippets__text {
    max-width: 65rem;
    font-size: 1.8rem;
    margin: 2.5rem auto;
  }
}
@media (min-width: 768px) {
  .snippets__grid {
    display: grid;
    grid-template-columns: repeat(2, 1fr);
  }
}

.snippet {
  padding: 2rem 0 4rem 0;
  display: flex;
  flex-direction: column;
  align-items: center;
}
@media (min-width: 768px) {
  .snippet {
    min-height: 65rem;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: flex-start;
    position: relative;
  }
}
@media (min-width: 768px) {
  .snippet__image {
    position: absolute;
    left: -21rem;
    max-width: 150%;
  }
}
@media (min-width: 1440px) {
  .snippet__image {
    top: 5rem;
    left: -8rem;
  }
}
.snippet__info {
  margin: 0 0 3.5rem 0;
  max-width: 40rem;
}
@media (min-width: 768px) {
  .snippet__info {
    text-align: left;
    padding-left: 6rem;
    max-width: 45rem;
  }
}
.snippet__title {
  font-size: 2.5rem;
  color: #4c545d;
  font-weight: 600;
  margin-bottom: 0.8rem;
}
@media (min-width: 768px) {
  .snippet__title {
    margin-bottom: 0;
  }
}
.snippet__text {
  margin: 0;
  font-size: 1.8rem;
}

.clipboard {
  text-align: center;
  padding: 10rem 2rem;
}
@media (min-width: 768px) {
  .clipboard {
    max-width: unset;
  }
}
.clipboard__description {
  font-size: 1.8rem;
}
@media (min-width: 768px) {
  .clipboard__description {
    max-width: 70rem;
    margin: 2rem auto;
  }
}
.clipboard__image {
  padding-top: 5rem;
  margin: 0 auto;
}

.workflow {
  text-align: center;
  max-width: 33rem;
  margin: 0 auto;
  padding-bottom: 10rem;
}
@media (min-width: 768px) {
  .workflow {
    max-width: unset;
    padding-bottom: 7rem;
  }
}
.workflow__description {
  margin-bottom: 10rem;
  font-size: 1.8rem;
}

@media (min-width: 768px) {
  .works__grid {
    display: grid;
    grid-template-columns: repeat(3, 1fr);
    gap: 1rem;
  }
}

.work {
  margin-bottom: 6rem;
}
@media (min-width: 1440px) {
  .work {
    padding: 0 2.5rem;
  }
}
.work__image {
  margin: 0 auto;
  height: 4rem;
}
.work__title {
  color: #4c545d;
  font-weight: 600;
  font-size: 2.2rem;
}
.work__text {
  margin-top: 0;
  font-size: 1.8rem;
}

.companies {
  display: grid;
  grid-template-columns: repeat(1, 1fr);
  padding: 5rem 0;
  gap: 5rem;
}
@media (min-width: 768px) {
  .companies {
    display: grid;
    grid-template-columns: repeat(5, 1fr);
    gap: 1rem;
  }
}
.companies__image {
  margin: 0 auto;
}

.OS {
  text-align: center;
  max-width: 30.5rem;
  margin: 8rem auto;
}
@media (min-width: 768px) {
  .OS {
    max-width: unset;
    max-width: 60rem;
  }
}
.OS__description {
  padding: 1rem 0 2rem 0;
  font-size: 1.8rem;
}
.OS__button--iOS {
  width: 90%;
  padding: 1.5rem 4rem;
  margin: 2.5rem auto;
  border-radius: 5rem;
  background-color: #26baa4;
  display: block;
  text-decoration: none;
  color: #FFF;
  box-shadow: 0 1.5rem 2rem #a6ede2;
}
@media (min-width: 768px) {
  .OS__button--iOS {
    display: inline-block;
    width: unset;
    margin: 2rem 1rem;
  }
}
.OS__button--iOS:hover {
  cursor: pointer;
  background-color: #2acfb7;
}
.OS__button--Mac {
  width: 90%;
  padding: 1.5rem 4rem;
  margin: 2.5rem auto;
  border-radius: 5rem;
  background-color: #6173ff;
  display: block;
  text-decoration: none;
  color: #FFF;
  box-shadow: 0 1.5rem 2rem white;
}
@media (min-width: 768px) {
  .OS__button--Mac {
    display: inline-block;
    width: unset;
    margin: 2rem 1rem;
  }
}
.OS__button--Mac:hover {
  cursor: pointer;
  background-color: #7a8aff;
}

.footer {
  background-color: #f4f5f6;
}
.footer__grid {
  margin: 10rem auto 0 auto;
  padding: 4rem 0;
  display: grid;
  grid-template-columns: repeat(1, 1fr);
  align-items: center;
}
@media (min-width: 768px) {
  .footer__grid {
    padding: 2rem 0;
    grid-template-columns: repeat(6, 1fr);
  }
}
.footer__logo {
  width: 10%;
  margin: 0 auto;
}
@media (min-width: 768px) {
  .footer__logo {
    width: 30%;
    margin: 0;
  }
}
.footer__links {
  display: flex;
  flex-direction: column;
  padding: 2.5rem 0;
}
@media (min-width: 768px) {
  .footer__links {
    flex-direction: row;
    justify-content: space-around;
    grid-column: 2/5;
  }
}
.footer__group {
  display: flex;
  flex-direction: column;
  align-items: center;
}
@media (min-width: 768px) {
  .footer__group {
    align-items: flex-start;
  }
}
.footer__link {
  padding: 1.5rem 0;
  text-decoration: none;
  color: #4c545d;
}
.footer__link:hover {
  color: #26baa4;
}
.footer__networks {
  display: flex;
  margin: 2rem auto;
  gap: 2rem;
}
@media (min-width: 768px) {
  .footer__networks {
    align-items: center;
    grid-column: 6/7;
  }
}
.footer__icon:hover {
  filter: invert(75%) sepia(70%) saturate(300%) hue-rotate(100deg) brightness(95%) contrast(100%);
}